<template>
  <button
    type="submit"
    class="
      px-5
      py-3
      bg-gradient-to-r
      from-red-400
      to-red-600
      font-semibold
      text-white
      rounded
      flex
    "
  >
    <div
      v-if="loading"
      class="
        w-6
        h-6
        mx-1
        border-4 border-t-4 border-white
        rounded-full
        animate-spin
        loader_btn
      "
    />
    {{ loading ? "Loading..." : text }}
  </button>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },

  watch: {
    loading: (n, o) => (n != o ? n : o),
  },
};
</script>

<style lang="scss" scoped>
.loader_btn {
  border-top-color: rgb(77, 75, 75) !important;
}
</style>