<template>
  <div
    class="min-h-screen bg-gradient-to-br from-red-400 to-red-500 mainscreen"
  >
    <Navbar />
    <div class="pb-10">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar";
export default {
  components: {
    Navbar,
  },
  methods: {
    navigateTocurrentPath() {
      let currentPath = localStorage.getItem("path");
      if (currentPath) {
        localStorage.removeItem("path");
        this.$router.replace(currentPath);
      }
    },
  },
  created() {
    this.navigateTocurrentPath();
  },
};
</script>

