import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./assets/tailwind.css";
import "./assets/global.css";
Vue.config.productionTip = false;

// vue moment for timestamp
// import moment from "moment";
import VueMoment from "vue-moment";
Vue.use(VueMoment);
Vue.moment().locale("en");
// lodash configuration
import lodash from "lodash";
import VueLodash from "vue-lodash";
Vue.use(VueLodash, { lodash });
import { auth } from "@/plugins/firebase";
// toast messages
import VueSwal from "vue-swal";
Vue.use(VueSwal);

// editor configuration
import katex from "katex";
import "katex/dist/katex.min.css";

Vue.use(katex);
window.katex = katex;
import "quill/dist/quill.core.css"; // import styles
import VueQuillEditor from "vue-quill-editor";
Vue.use(VueQuillEditor);

Vue.component("tbtn", require("./components/TButton.vue").default);
Vue.component("rbtn", require("./components/LButton.vue").default);
Vue.component("loader", require("./components/Loader.vue").default);

let app;
auth.onAuthStateChanged((user) => {
  if (user) {
    localStorage.setItem("acuser", user.email);
  }
  if (!app) {
    app = new Vue({
      router,
      store,

      render: (h) => h(App),
    }).$mount("#app");
  }
});
